@charset "UTF-8";

.portfolio {
    @include flex(flex, center, center, none, column, 15px);
    position: relative;

    .portfolio__section {
        padding: 0 10px;

        .portfolio__title {
            font-size: 2.8rem;
            margin-bottom: 5px;
        }

        .portfolio__subtitle {
            margin-left: 5px;
        }

        &:first-child {
            padding: 300px 0 180px 10px;

        }

        &:last-child {
            padding: 0;
            position: relative;

            &::before {
                content: 'RECENT WORKS';
                position: absolute;
                top: -10px;
                left: 10px;
                font-size: 1rem;
                box-shadow: inset 0 -5px 0px 0px #ff184e;
            }
        }

        .portfolio__list {
            @include flex(flex, flex-start, center, none, row, 15px);
            margin: 40px 5px;

            button {
                font-size: .80rem;
            }
        }

        .current {
            font-weight: bold;
            color: $clr-primary;
        }

        .portfolio__container {
            @include flex(flex, flex-start, flex-start, wrap, column, 0);
            padding: 0 25px 30px 25px;

            .portfolio__article {
                @include flex(inherit, space-between, flex-start, nowrap, row, 50px);
                padding: 20px 0;
                width: 100%;
                position: relative;

                figure {
                    box-shadow: 0 0 11px 0 #00000099;
                    img {
                       width: 255px;
                       height: 170px;
                       display: block;
                    }
                }

                .article__div{
                    position: relative;
                    &:hover{
                        & span{
                            transform: scale(100%);
                        }
                    }
                    &>figure{
                        border-radius: 10px;
                        &>img{
                            border-radius: inherit;
                        }
                    }
                    &>span{
                        @include flex(flex, center, center, wrap, column, 0);
                        background-color: #ff184ece;
                        border-radius: 10px;
                        position: absolute;
                        left: 15px;
                        right: 15px;
                        top: 15px;
                        bottom: 15px;
                        z-index: 90;
                        transition: all 180ms ease-in-out;
                        transform: scale(0%);
                        & svg{
                            font-size: 1.4rem;
                            color: $clr-elements-bg;
                        }
                    }
                }

                &>div {
                    @include flex(flex, space-between, flex-start, wrap, column, 10px);
                    flex-basis: 20%;

                    &>h2 {
                        @include flex(inherit, space-between, center, nowrap, row, 20px);
                        width: 100%;
                        font-weight: bold;
                        font-size: 1.1rem;
                        line-height: .5cm;
                        &>span{
                            font-size: .85rem;
                        }
                    }

                    h3 {
                        color: $clr-primary;
                        font-weight: bold;
                        font-size: .90rem;
                        text-transform: uppercase;
                    }
                }

                &>p {
                    flex-basis: 100%;
                    text-align: left;
                    line-height: .7cm;
                    font-size: .90rem;
                }

                .site {
                    text-decoration: none;
                    text-align: center;
                    border: 2px solid $clr-secondary;
                    border-radius: 20px;
                    font-size: .80rem;
                    font-weight: bold;
                    padding: 6px;
                    width: 230px;

                    &:hover {
                        border: 2px solid $clr-primary;
                        color: $clr-primary;
                    }
                }
                .github{
                    font-size: .85rem;
                    position: absolute;
                    right: 10px;
                    bottom: 10px;
                    text-decoration: none;
                    &:hover{
                        text-decoration: underline;
                        color: $clr-primary;
                    }
                }

                &+.portfolio__article {
                    border-top: 1px solid #1818182f;
                }

                .slide-modal{
                    @include flex(none, center, center, wrap, column, 0);
                    background-color: $clr-slide-bg;
                    position: fixed;
                    top: 0;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    z-index: 103;

                    .slider{
                        @include flex(flex, flex-start, flex-start, nowrap, column, 30px);
                        position: relative;
                        &>button{
                            position: absolute;
                            top: -40px;
                            right: -35px;
                            &>svg{
                                font-size: 2rem;
                                color: $clr-elements-bg;
                                &:hover{
                                    color: $clr-primary;
                                }
                            }
                        }

                        &>header, &>footer{
                            width: 100%;
                        }

                        &>header{
                            background-color: $clr-elements-bg;
                            border: 10px solid $clr-primary;
                            padding: 15px;
                            &>figure{
                                width: 680px;
                                height: 400px;
                                img{
                                    @include img(100%, 100%, 100%, 100%, initial, block);
                                }
                            }
                        }
                        &>footer{
                            @include flex(inherit, center, center, nowrap, row, 20px);
                            &>button{
                                border-radius: 20px;
                                padding: 6px;
                                background-color: $clr-elements-bg;
                                &:first-child{
                                    background-color: $clr-primary;
                                }
                                &:hover{
                                    background-color: $clr-primary;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 599px){
    .portfolio{
        .portfolio__section{
            .portfolio__container {
                .portfolio__article {
                    .slide-modal{
                        .slider{
                            margin: 0 30px;
                            &>button{
                                top: -40px;
                                right: -15px;
                            }
                        }
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 600px) and (max-width: 900px){
    .portfolio{
        .portfolio__section{
            .portfolio__container {
                .portfolio__article {
                    .slide-modal{
                        .slider{
                            margin: 0 80px;

                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 900px){
    .portfolio{
        .portfolio__section{
            .portfolio__title {
                font-size: 2.6rem;
            }
            &:first-child {
                padding: 200px 0 120px 10px;
            }

            .portfolio__list {
                @include flex(flex, flex-start, center, wrap, row, 15px);
            }
            .portfolio__container {
                @include flex(flex, flex-start, flex-start, wrap, column, 0);
                padding: 0 25px 30px 25px;

                .portfolio__article {
                    @include flex(inherit, flex-start, flex-start, wrap, column, 30px);
                    padding: 30px 0;
                    figure {
                        img {
                           width: 100%;
                           height: initial;
                        }
                    }

                    &>div {
                        @include flex(flex, space-between, flex-start, wrap, column, 20px);
                        flex-basis: initial;

                        &>h2 {
                            font-size: 1.2rem;
                            &>span{
                                font-size: .95rem;
                            }
                        }

                        h3 {
                            font-size: .90rem;
                        }

                    }

                    &>p {
                        flex-basis: initial;
                        line-height: .8cm;
                        font-size: .90rem;
                        overflow: auto;
                        max-height: 180px;
                    }

                    .site {
                        width: 140px;
                    }

                    .slide-modal{
                        .slider{
                            &>button{
                                &>svg{
                                    font-size: 1.8rem;
                                }
                            }
                            &>header{
                                &>figure{
                                    width: 100%;
                                    height: initial;
                                }
                            }
                            &>footer{
                                &>button{
                                    padding: 5px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 901px) and (max-width: 1300px){
    .portfolio{
        .portfolio__section{
            .portfolio__container {

                .portfolio__article {
                    @include flex(inherit, flex-start, flex-start, wrap, column, 10px);
                    figure {
                        img {
                           width: 480px;
                           height: 320px;
                        }
                    }
                    &>div {
                        @include flex(flex, space-between, flex-start, wrap, column, 30px);
                        flex-basis: initial;
                    }
                    &>p {
                        overflow: auto;
                        max-height: 200px;
                        margin: 25px 0;
                        font-size: 1rem;
                    }
                }
            }
        }
    }
}